import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';
import drumData from './drumData';
import DrumPad from './DrumPad';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }
  componentDidMount() {
    document.addEventListener("keydown", this.handleKeyPress)
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyPress)
  }
  handleKeyPress(event) {
    const availKeys = ["Q", "W", "E", "A", "S", "D", "Z", "X", "C"];
    const availKeysCodes = availKeys.map(key => key.charCodeAt());

    if(availKeysCodes.includes(event.keyCode)) {
      let keyPressed = document.getElementById(event.key.toUpperCase());
      keyPressed.play();
      keyPressed.parentElement.classList.add("keyPressed");
      setTimeout(function() { keyPressed.parentElement.classList.remove("keyPressed")}, 200);
    }
  }
  handleClick(id) {
    let buttonClicked = document.getElementById(id);
    buttonClicked.firstChild.play();
  }

  render() {
    const drumSet = drumData.map(item => <DrumPad key={item.id} drum={item} handleClick = {this.handleClick}/>);

    return (
      <div id="drum-machine">
        <div className="grid">
          {drumSet}
        </div>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
