import React from 'react';

function DrumPad(props) {
    return (
      <div>
        <button
          className="drum-pad"
          id={props.drum.id}
          onClick={() => props.handleClick(props.drum.id)} >
          <audio
            className="clip"
            id={props.drum.keyPressed}
            onKeyDown={props.handleKeyPress}
            src={props.drum.audioClip}
            type="audio/mpeg" >
            <p>Your browser doesn't support HTML5 audio.</p>
          </audio>
          {props.drum.keyPressed}
        </button>
      </div>
    );
}

export default DrumPad;
